<template>
  <div class="box">
    <canvas v-if="alpha" ref="canvas"></canvas>
    <transition name="transit">
      <div v-if="show" class="center">
        <div class="flex">
          <div class="left">
            <el-image style="width: 160px; height: 160px" :src="url" fit="fit"></el-image>
            <div class="textTitle">FXTwink</div>
            <div class="contact">
            <el-image @click.native="goToHttp('https://w---m.yuque.com/r/organizations/homepage')" class="contactImg yuyue" :src="require('../assets/yuque.png')" fit="fit" ></el-image>
            <el-image @click.native="goToHttp('https://github.com/FXTwink')" class="contactImg gubhit" :src="require('../assets/github.png')" fit="fit"></el-image>
            <el-image @click.native="copy('fxs@fx-studio.cn')" class="contactImg email" style="" :src="require('../assets/email.png')" fit="fit" ></el-image>
            </div>
          </div>
          <div class="right">
            <div class="text-white">工作区</div>
            <div class="text">电子项目集</div>
            <div class="text">客制化键盘</div>
            <div class="text-white">相关文章</div>
            <!-- <div class="text">A comprehensive guide to creating intuitive context menus</div>
            <div class="text">Scrollbars in Web applications</div>
            <div class="text">Product design of Stripe Dashboard for iPhone</div> -->
          </div>
        </div>
      </div>
    </transition>
    
  </div>
</template>

<script>
export default {
  name: 'ParticleComponent',
  data() {
    return {
      url: require('../assets/LOGO_W.png'),
      show: false,
      PARTICLE_NUM: 500, // 粒子数量
      PARTICLE_BASE_RADIUS: 0.5, // 半径
      FL: 500,
      DEFAULT_SPEED: 50, // 粒子运动速度慢
      BOOST_SPEED: 200,  // 粒子运动速度快
      canvas: null,
      canvasWidth: 0,
      canvasHeight: 0,
      context: null,
      centerX: 0,
      centerY: 0,
      // mouseX: 0,
      // mouseY: 0,
      alpha: 0.1, // 透明度
      speed: 2,
      targetSpeed: 2,
      particles: [],
      intervalId: null
    };
  },
  mounted() {
    this.canvas = this.$refs.canvas;
    this.resize();
    // 处理窗口缩放时要处理的逻辑操作
    window.addEventListener('resize', this.resize);
    // 生成粒子
    for (let i = 0; i < this.PARTICLE_NUM; i++) {
      let particle = new Particle();
      this.particles.push(this.randomizeParticle(particle));
      this.particles[i].z -= 500 * Math.random(); //  Math.random() 返回一个包括0但小于1的随机数
    }
    // 循环执行 setInterval，另一个是定时执行 setTimeout
    this.intervalId = setInterval(this.loop, 1000 / 60);
    setTimeout(() => {
      this.accelerateParticles();
    }, 1);
    setTimeout(() => {
      this.fadeOutParticles();
    }, 1500);
    setTimeout(() => {
      this.clearParticles()
    }, 3000);
    setTimeout(() => {
      this.show = true
      clearInterval(this.intervalId);
    }, 3500);
    // this.$nextTick(() => {
    //   this.opacityStyle.opacity = 1;
    // });
  },
  // 实例销毁之前调用 
  beforeDestroy() {
    window.removeEventListener('resize', this.resize);

    clearInterval(this.intervalId);
  },
  methods: {
    resize() {
      this.canvasWidth = this.canvas.width = window.innerWidth;
      this.canvasHeight = this.canvas.height = window.innerHeight;
      this.centerX = this.canvasWidth * 0.5;
      this.centerY = this.canvasHeight * 0.5;
      this.context = this.canvas.getContext('2d');
      this.context.fillStyle = 'rgb(255, 255, 255)';
    },
    // 执行每一帧的绘制逻辑，包括粒子的移动、重新生成和绘制
    loop() {
      // 将当前的绘图状态（包括所有的绘图属性如填充样式、笔触样式、阴影、变换矩阵等）保存到一个栈中
      
      if(this.alpha <= 0.1){
        this.alpha = 0
      }
      this.context.globalAlpha = this.alpha;
      this.context.save();
      this.context.fillStyle = 'rgb(0, 0, 0)';
      this.context.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
      // 从栈中弹出最近保存的绘图状态，并将其应用到当前的绘图上下文
      this.context.restore();
      this.speed += (this.targetSpeed - this.speed) * 0.01;
      // this.speed = this.targetSpeed
      const halfPi = Math.PI * 0.5;
      const atan2 = Math.atan2;
      const cos = Math.cos;
      const sin = Math.sin;

      this.context.beginPath(); // 设置弧形
      for (let i = 0; i < this.PARTICLE_NUM; i++) {
        let p = this.particles[i];

        p.pastZ = p.z;
        p.z -= this.speed;

        if (p.z <= 0) {
          this.randomizeParticle(p);
          continue;
        }

        let cx = this.centerX  * 1.25;
        let cy = this.centerY  * 1.25;

        let rx = p.x - cx;
        let ry = p.y - cy;

        let f = this.FL / p.z;
        let x = cx + rx * f;
        let y = cy + ry * f;
        let r = this.PARTICLE_BASE_RADIUS * f;

        let pf = this.FL / p.pastZ;
        let px = cx + rx * pf;
        let py = cy + ry * pf;
        let pr = this.PARTICLE_BASE_RADIUS * pf;

        let a = atan2(py - y, px - x);
        let a1 = a + halfPi;
        let a2 = a - halfPi;

        // 移动到路径的起始点
        this.context.moveTo(px + pr * cos(a1), py + pr * sin(a1));
        // 从上一个位置绘制一个弧线到当前粒子位置
        this.context.arc(px, py, pr, a1, a2, true);
        // 从当前位置为起点，画一条直线到新的位置外围
        this.context.lineTo(x + r * cos(a2), y + r * sin(a2));
        // 绘制从新位置的外围回到起始角度的弧线
        this.context.arc(x, y, r, a2, a1, true);
        // 关闭路径，将起点和终点通过直线连接起来
        this.context.closePath();
      }
      this.context.fill();
    },
    // 随机化粒子的初始位置和深度
    randomizeParticle(p) {
      p.x = Math.random() * this.canvasWidth;
      p.y = Math.random() * this.canvasHeight;
      p.z = Math.random() * 1500 + 500;
      return p;
    },
    accelerateParticles() {
      this.targetSpeed = this.DEFAULT_SPEED;
      const step = () => {
        if( this.alpha <= 1){
          this.alpha += 0.03;
          this.alpha = this.alpha >= 1 ? 1 :this.alpha
          requestAnimationFrame(step);
        }
      }
      step();
    },
    fadeOutParticles() {
      this.targetSpeed = this.BOOST_SPEED;
    },
    clearParticles(){
      this.targetSpeed = this.DEFAULT_SPEED;
      const step = () => {
        if(this.alpha >= 0){
          this.alpha -= 0.1
          this.alpha = this.alpha <= 0 ? 0 :this.alpha
          requestAnimationFrame(step);
        }
      }
      step();
    },
    goToHttp(link){
      console.log(111)
      window.open(link, '_blank');
    },
    copy(link){
      let url = link;
        let oInput = document.createElement('input');
        oInput.value = url;
        document.body.appendChild(oInput);
        oInput.select(); // 选择对象;
        console.log(oInput.value)
        document.execCommand("Copy"); // 执行浏览器复制命令
        this.$message({
          message: '邮箱复制成功'
        });
        oInput.remove()
    }
  }
};
// 创建粒子对象，每个粒子都有 x、y 和 z 坐标以及一个 pastZ 属性，表示粒子上一次的位置
function Particle(x, y, z) {
  this.x = x || 0;
  this.y = y || 0;
  this.z = z || 0;
  this.pastZ = 0;
}
</script>

